.ToDoTable {   
  width: 100%;
  margin: auto;
  border-collapse: collapse;
  border-spacing: 0;
  background-color: white;
  -webkit-box-shadow: 0 0 0 10px #eeeced;
          box-shadow: 0 0 0 10px #eeeced;
}

.TableContainer {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

@media (min-width: 500px) {
  .ToDoTable {
    width: 80%;
  }
}

@media (min-width: 1000px) {
  .ToDoTable {
    width: 800px;
  }
}


